/*
	btn.scss
---------------------------------------------------------------------------- */
%buttonBase {
    border: none;
    width: 15.13vw;
    height: 4.88vw;
    cursor: pointer;
    color: #FFF;
    font-size: 1.46vw;
    font-weight: bold;
}
.p--button {
    &Wrapper {
        margin: 3.9vw 0;
        text-align: center;
        letter-spacing: -0.4em;
        li {
            display: inline-block;
            letter-spacing: normal;
            margin: 0 1.70vw;
        }
    }
    &Back {
        @extend %buttonBase;
        background-color: #838383;
    }
    &Secondry {
        @extend %buttonBase;
        background-color: #838383;
    }
    &Primary {
        @extend %buttonBase;
        background-color: #e56600;
    }
}
@include sp {
    %buttonBase {
        width: 35.5vw;
        height: 13.28vw;
        font-size: 4.37vw;
    }
    .p--button {
        &Wrapper {
            margin: 6.96vw 0;
            li {
                margin: 0 2.125vw;
            }
        }
    }
}


