@charset "utf-8";
/*
    menu.scss
---------------------------------------------------------------------------- */

/*
     MenuPanel
--------------------------------------------------------------*/
.l--menu {
    &Wrapper {
        width: 80.666vw;
        margin: 0 auto;
    }
    &__box {
        display: block;
        width: 25vw;
        height: 18.55vw;
        float: left;
        background-color: #333;
        margin: 0 2.83vw 2.83vw 0;
        &:link {color: #FFF; text-decoration: none;}
        &:visited {color: #FFF; text-decoration: none;}
        &:hover {color: #FFF; text-decoration: none;}
        &:nth-child(3n) {
            margin: 0 0 2.83vw;
        }
        &Image {
            min-height: 11.621vw;
            position: relative;
            img {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
                height: 5.71vw;
            }
        }
        &Title {
            height: 6.835vw;
            background-color: #494a45;
            font-size: 1.66vw;
            text-align: center;
            line-height: 6.835vw;
            .exLinks {
                display: inline-block;
                background: url(../images/common/m_icon06.png) no-repeat;
                background-position: right center;
                background-size: 1.464vw;
                padding: 0 1.935vw 0 0;
            }
        }
        &.disableBox {
            background-color: #969696;
            .l--menu__boxTitle {
                background-color: #969696;
                color: #FFF;
            }
        }
    }
}
@include sp {
    .l--menu {
        &Wrapper {
            width: 87.5vw;
        }
        &__box {
            width: 42.1875vw;
            height: 34.687vw;
            margin: 0 3.125vw 3.125vw 0;
            &:nth-child(3n) {
                margin: 0 3.125vw 3.125vw 0;
            }
            &:nth-child(even) {
                margin: 0 0 3.125vw;
            }
            &Image {
                min-height: 18.75vw;
                img {
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    margin: auto;
                    height: 11.71vw;
                }
            }
            &Title {
                height: 15.625vw;
                display: table-cell;
                width: 100vw;
                vertical-align: middle;
                font-size: 4.375vw;
                line-height: 1.25;
                .exLinks {
                    background-size: 4.0625vw;
                    padding: 0 6.25vw 0 0;
                }
            }
        }
    }
}
/*
     Hamburger Menu
--------------------------------------------------------------*/
.body--fixed {
    position: fixed;
    width: 100%;
    height: 100%;
}
.burger--wrap {
    float: right;
    height: 2.92vw;
    width: 2.92vw;
    position: relative;
    top: 0;
    right: 0;
    }
.burger {
    width: 2.92vw;
    height: 2.92vw;
    position: relative;
    display: flex;
    align-items: center;
    top: 0;
    right: 0;
    z-index: 9999;
    cursor: pointer;
}
.burger div {
    width: 1.12vw;
    height: 1.12vw;
    background: #FFF;
    transition: 0.2s;
    position: absolute;
}
.burger .lupSquares { top: 0; left: 0;}
.burger .rupSquares { top: 0; right: 0;}
.burger .lunSquares { bottom: 0; left: 0;}
.burger .runSquares { bottom: 0; right: 0;}
.burger-active .lupSquares { transform: rotate(135deg); top: 0; bottom: 0; left: 1.25vw; margin: auto; width: 0.29vw; height: 3.80vw;}
.burger-active .runSquares { transform: rotate(-135deg); top: 0; bottom: 0; right: 1.25vw; margin: auto; width: 0.29vw; height: 3.80vw;}
.burger-active .rupSquares {opacity: 0;}
.burger-active .lunSquares {opacity: 0;}
.navigation {
    margin: 2.18vw 0 0;
    z-index: 300;
    width: 100vw;
    height: 100vh;
    position: fixed;
    right: -100vw;
    transition: 0.6s;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    background-color: rgba(230,230,230,1.0);
    padding: 10.0vw 0 0;
    transition: 0.5s;
}
 .burger-active > .line {
    transform: scale(0);
}
.nav-active {
    right: 0;
}
@include sp {
    .burger--wrap {
        height: 7.8125vw;
        width: 7.8125vw;
        }
    .burger {
        width: 7.8125vw;
        height: 7.8125vw;
    }
    .burger div {
        width: 2.96vw;
        height: 2.96vw;
    }
    .burger-active .lupSquares { transform: rotate(135deg); top: 0; bottom: 0; left: 3.25vw; margin: auto; width: 0.85vw; height: 7.80vw;}
    .burger-active .runSquares { transform: rotate(-135deg); top: 0; bottom: 0; right: 3.25vw; margin: auto; width: 0.85vw; height: 7.80vw;}
    .burger-active .rupSquares {opacity: 0;}
    .burger-active .lunSquares {opacity: 0;}
    .navigation {
        margin: 3.98vw 0 0;
        padding: 6.6vw 0 0;
    }
}