/*--------------------------------------------------------------
    # header.scss
--------------------------------------------------------------*/
.l--header {
    &Meta {
        padding: 0 2.92vw;
        min-height: 4.39vw;
        &Title {
            float: left;
            font-size: 1.625vw;
            line-height: 1.0;
            font-family: $webfontMincho;
            padding: 1.464vw 0 0;
        }
        &User {
            float: right;
            font-size: 1.269vw;
            line-height: 1.0;
            padding: 1.464vw 0 0;
        }
    }
    &Head {
        background-color: #333;
        box-sizing: content-box;
        border-bottom: 0.39vw solid #e53f0a;
        height: 6.44vw;
        &__title {
            line-height: 2.92vw;
            color: #FFF;
            font-weight: normal;
            font-size: 2.441vw;
            padding: 0;
            margin: 0;
            float: left;
        }
        &__inner {
            position: relative;
            padding: 1.70vw 2.92vw;
        }
    }
}
body.is--fixed .l--headerHead {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 150;
}
body.is--fixed .l--user {
    padding: 8.347vw 2.92vw 1.46vw;
}
.l--user {
    padding: 1.46vw 2.92vw 1.46vw 0;
    text-align: right;
    line-height: 1.0;
    margin: 0 0 2.44vw;
    &Account {
        padding: 0;
        margin: 0;
    }
}
@include sp {
    .l--header {
        &Meta {
            padding: 0 3.90vw;
            min-height: 10.93vw;
            &Title {
                font-size: 4.0625vw;
                padding: 3.90vw 0 0;
            }
            &User {
                font-size: 3.75vw;
                padding: 3.90vw 0 0;
            }
        }
        &Head {
            height: 15.625vw;
            border-bottom: 0.9375vw solid #e53f0a;
            &__title {
                font-size: 5.9375vw;
                line-height: 7.81vw;
            }
            &__inner {
                position: relative;
                padding: 4.68vw 3.90vw;
            }
        }
    }
    .l--user {
        padding: 4.6875vw 3.90vw;
        text-align: left;
        font-size: 3.75vw;
        &Account {
            padding: 0;
            margin: 0;
        }
    }
}