/*
    table.scss
---------------------------------------------------------------------------- */
table {
    width: 100%;
    border: 1px solid #333;
    border-collapse: collapse;
    border-spacing: 0;
    margin: 0 0 1.95vw;
    th{
        background-color: #f4d767;
        vertical-align: middle;
        border: 1px solid #333;
        text-align: center;
        padding: 0.73vw 0;
        font-size: 1.26vw;
        position: relative;
        background-clip: padding-box;
    }
    td{
        background-color: #FFF;
        vertical-align: middle;
        border: 1px solid #333;
        padding: 0.73vw;
        font-size: 1.26vw;
    }
    .tabletData {
        text-align: center;
    }
    .status {
        &High {background-color: #e56600;}
        &Low {background-color: #b9b9b9;}
    }
}